<h2 id="within-navbar">Within navbar <a class="header-anchor" href="#within-navbar" aria-hidden="true">&#128279;</a></h2>
<dropdown-within-navbar ref="dropdown-within-navbar"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">navbar</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"brand"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"navbar-brand"</span> <span class="hljs-attr">role</span>=<span class="hljs-string">"button"</span>&gt;</span>Brand<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"collapse"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">navbar-nav</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">dropdown</span> <span class="hljs-attr">tag</span>=<span class="hljs-string">"li"</span>&gt;</span>
