<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference" aria-hidden="true">&#128279;</a></h1>
<h2 id="dropdown-2"><a href="https://github.com/wxsms/uiv/blob/master/src/components/dropdown/Dropdown.vue">Dropdown</a> <a class="header-anchor" href="#dropdown-2" aria-hidden="true">&#128279;</a></h2>
<h3 id="props">Props <a class="header-anchor" href="#props" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>v-model</code></td>
<td>Boolean</td>
<td></td>
<td></td>
<td>Indicate the dropdown status (show / hide).</td>
</tr>
<tr>
<td><code v-pre>tag</code></td>
<td>String</td>
<td>div</td>
<td></td>
<td>The HTML tag that render the dropdown component.</td>
</tr>
<tr>
<td><code v-pre>append-to-body</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Append the dropdown slot to body.</td>
</tr>
<tr>
<td><code v-pre>dropup</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Use dropup style.</td>
</tr>
<tr>
<td><code v-pre>menu-right</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Use dropdown-menu-right style.</td>
</tr>
<tr>
<td><code v-pre>disabled</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Disable the dropdown.</td>
</tr>
<tr>
<td><code v-pre>not-close-elements</code></td>
<td>Array</td>
<td></td>
<td></td>
<td>Pass an array of element which the dropdown will not close on click.</td>
</tr>
<tr>
<td><code v-pre>position-element</code></td>
<td>Element</td>
<td>this.$el</td>
<td></td>
<td>Pass an HTML element which the dropdown will be positioned by (in append-to-body mode).</td>
</tr>
</tbody>
